import React, { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { fetchParentAction, fetchUpdateParentAction } from "../../../../app/service/action/parentAction"
import TitleComponent from "../../../../components/Title/TitleComponent"
import Loading from "../../../../components/Loading/Loading"
import InputText from "../../../../components/Input/InputText"


const INITIAL_ACCOUNT_OBJ = {
  userId: '',
  id: '',
  fatherName: '',
  fatherPhone: '',
  motherName: '',
  motherPhone: '',
  address: ''
}
const Parent = () => {
  const dispatch: any = useDispatch()
  const [requestParam, setRequestParams] = useState(INITIAL_ACCOUNT_OBJ)

  const updateFormValue = ({ updateType, value }) => {
    // setErrorMessage("")
    setRequestParams({ ...requestParam, [updateType]: value })
  }

  const data = useSelector(({ parentState }) => {
    return parentState.parent.data;
  })
  const status = useSelector(({ parentState }) => {
    return parentState.parent.status;
  })

  const isLoading = useSelector(({ parentState }) => {
    return parentState.parent.isLoading;
  })

  useEffect(() => {
    dispatch(fetchParentAction())
  }, [dispatch]);

  useEffect(() => {
    if (status === "ready" && data.result) {
      setRequestParams(data.result)
    }
  }, [data.result, status]);

  const handleUpdate = () => {
    dispatch(fetchUpdateParentAction(requestParam))
    dispatch(fetchParentAction())
  }

  return (
    <div className="parent-page">
      <TitleComponent title={'Data Orang Tua'} />
      <hr />
      {isLoading ? <Loading /> :
        <div className="data-parent">
          <InputText labelTitle={'Nama Ayah'} updateType={'fatherName'}
            defaultValue={data.result.fatherName}
            updateFormValue={updateFormValue} />
          <InputText labelTitle={'No. HP/Whatsapp Ayah'} updateType={'fatherPhone'}
            defaultValue={data.result.fatherPhone}
            updateFormValue={updateFormValue} />
          <InputText labelTitle={'Nama Ibu'} updateType={'motherName'}
            defaultValue={data.result.motherName}
            updateFormValue={updateFormValue} />
          <InputText labelTitle={'No. HP/Whatsapp Ibu'} updateType={'motherPhone'}
            defaultValue={data.result.motherPhone}
            updateFormValue={updateFormValue} />
          <InputText labelTitle={'Alamat'} updateType={'address'}
            defaultValue={data.result.address}
            updateFormValue={updateFormValue} />

          <button className={'btn-update'}
            onClick={handleUpdate}>UPDATE</button>
        </div>
      }
    </div>
  );
}

export default Parent
