import { REQUEST_PARENT, REQUEST_PARENT_FAILED, REQUEST_PARENT_SUCCESS } from "../constant/UsersConstant";
import { apiBaseUrl } from "../apiBaseUrl";
import { getError, getResponseError } from "../helper/handlingError";

export const fetchParentAction = () => {
    return async (dispatch) => {
        dispatch({
            type: REQUEST_PARENT,
        });

        try {
            const userDetail = JSON.parse(localStorage.getItem('userDetail'));
            const token = localStorage.getItem('token');

            const { data, status } = await apiBaseUrl({
                method: 'GET',
                params: {},
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${token}`,
                },
                data: {},
                url: `/api/v2/parent/${userDetail.id}`,
            });

            // if (data.result == null) {
            //     dispatch({
            //         type: REQUEST_PARENT_FAILED,
            //     });
            //     return getResponseError(data.responseData.message);
            // }

            if (status === 200) {
                dispatch({
                    type: REQUEST_PARENT_SUCCESS,
                    payload: data,
                });
                return data.result;
            }
        } catch (error) {
            dispatch({
                type: REQUEST_PARENT_FAILED,
            });
            await getError(error);
        }
    };
};

export const fetchUpdateParentAction = (requestParam) => {
    return async (dispatch) => {
        dispatch({
            type: REQUEST_PARENT,
        });
        const token = localStorage.getItem('token');
        try {
            const { data, status } = await apiBaseUrl({
                method: 'PATCH',
                params: {},
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${token}`,
                },
                data: requestParam,
                url: `/api/v2/parent/${requestParam.id}`,
            });

            if (data.result == null) {
                dispatch({
                    type: REQUEST_PARENT_FAILED,
                });
                return getResponseError(data.responseData.message);
            }

            if (status === 200) {
                dispatch({
                    type: REQUEST_PARENT_SUCCESS,
                    payload: data,
                });
                return data.result;
            }
        } catch (error) {
            dispatch({
                type: REQUEST_PARENT_FAILED,
            });
            await getError(error);
        }
    };
};
